div.tfw-behavior-touchable {
  position      : absolute;
  overflow      : hidden;
  z-index       : 999999;
  pointer-events: none;
  background    : transparent;
}
div.tfw-behavior-touchable > div {
  position        : absolute;
  border-radius   : 100%;
  opacity         : .7;
  -webkit-transform       : scale(0);
          transform       : scale(0);
  background-color: #000;
}
div.tfw-behavior-touchable > div.open {
  opacity                   : .1;
  -webkit-transform                 : scale(1);
          transform                 : scale(1);
  transition                : all .5s ease;
  transition-timing-function: cubic-bezier(0.7, 1, .5, -.5);
}
div.tfw-behavior-touchable > div.show {
  -webkit-transform : scale(1);
          transform : scale(1);
  transition: all .2s ease;
}

@-webkit-keyframes tfw-view-icon-rotation {
    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

@keyframes tfw-view-icon-rotation {
    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

.tfw-view-icon {
    display: inline-block;
    transition: all .3s;
    margin: 2px;
    padding: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

.tfw-view-icon.zero {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
}

.tfw-view-icon.active {
    cursor: pointer;
}

.tfw-view-icon.animate {
    animation: tfw-view-icon-rotation 1.1s linear infinite;
    -webkit-animation: tfw-view-icon-rotation 1.1s linear infinite;
}

.tfw-view-icon.disabled {
    cursor: default;
    opacity: .5;
}

