    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    max-height: calc(100vh - 16px);
    max-width: calc(100vw - 16px);
}

