    text-overflow: ellipsis;
    white-space: nowrap;
    flex: 1 1 auto;
}

div.webBrayns-view-ModelButton div.name > div.invisible {
    font-style: italic;
}

div.webBrayns-view-ModelButton div.name > div.visible {
    font-weight: bold;
}

div.tfw-view-list {
  position       : relative;
  overflow       : auto;
  padding        : 0;
  margin         : 1rem;
  box-shadow     : 0 4px 8px #000;
  min-width      : 280px;
  max-height     : 100vh;
  /* To prevent a bug in Google Chrome */
  overflow-anchor: none;
}
div.tfw-view-list.refreshing > div.body {
  -webkit-filter : blur(8px);
          filter : blur(8px);
  transition    : none;
  pointer-events: none;
}
div.tfw-view-list > div {
  margin   : 0;
  display  : block;
  width    : 100%;
  min-width: 100%;
}
div.tfw-view-list > div.body {
  -webkit-filter : blur(0);
          filter : blur(0);
  overflow: hidden;
}
div.tfw-view-list > div.body > div.item {
  display        : flex;
  justify-content: flex-start;
  align-items    : center;
  flex-direction : row;
  margin         : 0;
  padding        : 0;
}
div.tfw-view-list > div.body > div.item > *,
div.tfw-view-list > div.body > div.item > div {
  width: 100%;
}
div.tfw-view-list > div.space {
  font-size          : 0;
  content            : '.';
  background-repeat  : repeat-y;
  background-position: 0 0;
  background-size    : 263px 36px;
  background-image   : url(data:image/jpeg;base64,);
}
div.tfw-view-list > div.screen {
  position      : absolute;
  left          : 0;
  top           : 0;
  right         : 0;
  bottom        : 0;
  pointer-events: none;
  z-index       : 1;
  text-align    : center;
  overflow      : hidden;
}
div.tfw-view-list > div.screen > button {
  -webkit-transform : translateY(-110%);
          transform : translateY(-110%);
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}
div.tfw-view-list.animate-refresh > div.screen > button {
  -webkit-transform : translateY(100%);
          transform : translateY(100%);
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

