html, body {
    font-family: josefin-sans;
}


#splash-screen.vanish {
    opacity: 0;
    transition: opacity .6s;
}


div.App {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

div.App {
    --panel-width: 400px;
}

div.App > div.view {
    position: absolute;
    top: 0;
    left: var(--panel-width);
    height: 100%;
    width: calc(100% - var(--panel-width));
    overflow: hidden;
    background: #500;
    display: block;
}

div.App > div.view > div:first-child {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: block;
}

div.App > div.panel > header {
    height: 2rem;
    flex: 0 0 auto;
    z-index: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 48px;
}

div.App > div.panel {
    position: absolute;
    left: 0;
    top: 0;
    width: var(--panel-width);
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}

div.App > div.panel > header > div {
    padding: 0 .5rem;
}

div.App > div.panel > div {
    height: calc(100% - 2rem);
}

div.App > div.panel > div > * {
    flex: 1 1 auto;
}

